.nav2 {
  display: flex;
  box-shadow: 0 15px 40px -20px rgba(40, 44, 63, 0.15);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: white;
}
.nav-left {
  display: flex;
  width: 40%;
}
.logo1 svg {
  width: 100px;
  height: 50px;
  margin: 15px 20px 15px 60px;
}
.location {
  color: #686b78;
  font-size: 14px;
  margin: auto 0;
}
.nav-right {
  display: flex;
  width: 60%;
  justify-content: space-around;
  margin: auto;
  padding: 0 5%;
  box-sizing: border-box;
}
.Search,
.offers,
.help,
.profile,
.Cart {
  color: #3d4152;
  font-size: 17px;
  font-weight: 500;
  cursor: pointer;
}
.mainBody {
  margin: auto;
  padding: 50px 0;
  box-sizing: border-box;
}
.hov:hover {
  color: #fc8019;
  fill: #fc8019;
}
.Cart {
  position: relative;
  top: 0;
}
.cartqu {
  position: absolute;
  top: -8px;
  left: 2px;
  color: #fc8019;
  width: 15px;
  height: 15px;
  /* background-color: #fc8019; */
  font-size: 14px;
  font-weight: 700;
  border-radius: 10px;
  text-align: center;
}
@media (max-width: 480px) {
  /* .nav2 {
    display: none;
  } */
  .nav-left {
    display: flex;
    width: 40%;
  }
  .logo1 svg {
    width: 50px;
    height: 50px;
    margin: 15px 20px 15px 30px;
    box-sizing: border-box;
  }
  .location {
    display: none;
  }
  .nav-right {
    display: flex;
    width: 60%;
    margin: auto;
    padding: 0 5%;
    box-sizing: border-box;
  }
  .Search,
  .offers,
  .help {
    display: none;
  }
}
